export default "/* Base styles */\r\n:host {\r\n  display: flex;\r\n  flex-direction: column;\r\n  height: calc(100vh - 25px); /* Adjust height to fit between header and footer */\r\n  width: 100%;\r\n  overflow: hidden;\r\n}\r\n.split-example {\r\n  height: 100%;\r\n  margin-left: 5px !important;\r\n}\r\n/* Split bar visibility */\r\n.HideSplitBar {\r\n  flex: 0 0 0 !important;\r\n}\r\n.HideSplitBar div {\r\n  display: none !important;\r\n}\r\n.showSplitBar div {\r\n  display: block !important;\r\n}\r\n/* Full-page and partial modes */\r\n.ShowFullPageMode {\r\n  order: 2;\r\n  flex: 0 0 calc(100% - 15px) !important;\r\n}\r\n/* Scrollbar styling */\r\n::-webkit-scrollbar {\r\n  width: 10px;\r\n}\r\n.setWindowHeight {\r\n  height: calc(100vh - 1px); /* Adjust height dynamically */\r\n  overflow-y: auto;\r\n}\r\n.scrollbar {\r\n  overflow-y: auto;\r\n  overflow-x: hidden;\r\n}\r\n/* Responsive adjustments for small screens */\r\n@media (max-width: 767px) {\r\n  .body-content {\r\n    padding-top: 50px; /* Space for the nav menu */\r\n  }\r\n}\r\n/* Responsive layout adjustments */\r\n@media only screen and (max-width: 600px) {\r\n  #leftMenu, #asSplitArea2 {\r\n    flex: 0 0 50%;\r\n  }\r\n}\r\n@media only screen and (min-width: 601px) and (max-width: 1200px) {\r\n  #leftMenu {\r\n    flex: 0 0 30%;\r\n  }\r\n  #asSplitArea2 {\r\n    flex: 0 0 70%;\r\n  }\r\n}\r\n@media only screen and (min-width: 1201px) {\r\n  #leftMenu {\r\n    flex: 0 0 25%;\r\n  }\r\n  #asSplitArea2 {\r\n    flex: 0 0 75%;\r\n  }\r\n}\r\n"